.deviceMap {
  width: 100%;
  height: calc(100% - 50px);;
  display: flex;
}

.deviceMapImage {
  color: aqua;
  stroke: aqua;
  //   position: relative;
  // background-color: rgb(28, 39, 25);
  // width:2500;
  // height:976;
}

.device {
  width: 44px;
  height: 44px;
  z-index: 99;
  stroke: rgba(172, 255, 47, 0.774);
  fill: rgba(172, 255, 47, 0.774);
  // stroke-width: 1;
}

.device:hover {
  fill: rgb(42, 255, 255);
  stroke: rgba(47, 255, 255, 0.774);
}

.device:active {
  fill: rgba(213, 47, 255, 0.774);
  stroke: rgba(213, 47, 255, 0.774);
}

.popupWindow {
  position: absolute;
  width: 480px;
  // height: 400px;
  border-radius: 4px;
  z-index: 1000;
  box-shadow: 0 11px 15px -7px rgba(0,0,0,.2), 0 24px 38px 3px rgba(0,0,0,.14), 0 9px 46px 8px rgba(0,0,0,.12);
}

.contextPanelLabel {
  text-align: center;
  margin-top: 6px;
}

.contextPanelLabelValues {
  margin-left: 6px;
}

.contextPanelCommands {
  height: 70%;
  margin: 4px;
  padding: 4px;
  background-color: rgba(221, 221, 221, 0.63);
}

.contextPanelCommandsLabel {
  text-align: center;
}

.toolBarCoordinates {
  display: flex;
  align-items: center;
  gap: 6px;
  padding-left: 6px;
  & .label {
    margin: 0;
    width: 100% !important;
  }
}

.dataGridFix {
  margin-top: 5px;
  margin-right: 5px;
}

.popupHeaderTitle {
  margin-top: 9px;
  text-align: center;
}

.popupValueWrapper {
  padding: 10px;
  margin-top: 0px !important;
}


