@import "./colorScheme";

.commonScreen {
  position: relative;
  height: 100vh;
  width: 100%;
  background-color: $master;
  color: $master-blue;
  &DarkCold {
    background-color: $master;
    color: $master-blue;
  }
  
  &LightCold{
    // background-color: $master-light-cold;
    color: rgb(255, 0, 0);
  }
}

.commonScreenContent {
  padding: 6px;
  position: relative;
  z-index: 100 !important;
  height: 100%;
  box-sizing: border-box;
}

.commonBackGroundImg {
  position: absolute;
  height: 80%;
  width: 80%;
  fill: rgb(94, 93, 93);
  z-index: 30;
  pointer-events: none;
}

.commonFloatingContent {
  opacity: 0.95;
  // width: 98%;
  margin-left: 1%;
}
.commonFloatingPanelH {
  opacity: 0.95;
  // width: 98%;
  margin-left: 1%;
  display: flex;
  align-items: center;
}

.commonItemsPanel {
  width: 98%;
  margin-left: 1%;
  display: flex;
  height: 50px;
  align-items: center;
  padding-left: 8px;
  // border: 1px solid white;
  // border-radius: ;
  background-color: rgba(240, 240, 240, 0.11);
}
.commonPanelItem {
  height: 34px;
  width: 80px;
  border: 1px solid rgba(255, 255, 255, 0.363);
  border-radius: 10%;
  // background-color: rgba(29, 18, 124, 0.671);
  background-color: $sb_btn_bg_non_active;
  margin-left: 4px;
  text-align: center;
}
.commonPanelItem:hover {
  background-color: rgba(10, 10, 87, 0.699);
  filter: drop-shadow(0px 1px 6px rgb(0, 255, 0));
}
.commonPanelItem:active {
  // filter: drop-shadow(0px 1px 4px rgb(0, 255, 0))  hue-rotate(120deg);
  filter: drop-shadow(0px 1px 16px rgb(4, 131, 4)) contrast(150%);
}

.commonFloatOverPanel {
  height: 100%;
  width: 30%;
  position: absolute;
  // top:40px;
  right: 0px;
  background-color: rgb(77, 79, 194);
  z-index: 999;
}

.green {
  color: $master-green;
  height: 100% !important;
  width: 100% !important;
}
.greenDark {
  background-color: $master-green-dark;
}
.blue {
  color: $master-blue;
  height: 100% !important;
  width: 100% !important;
}
.orange {
  color: $master-orange;
  height: 100% !important;
  width: 100% !important;
}
.red {
  color: $master-red;
  height: 100% !important;
  width: 100% !important;
}
