.deviceMap {
  width: 100%;
  height: calc(100% - 50px);;
  display: flex;
}

.deviceMapImage {
  color: aqua;
  stroke: aqua;
  //   position: relative;
  // background-color: rgb(28, 39, 25);
  // width:2500;
  // height:976;
}

.device {
  width: 44px;
  height: 44px;
  z-index: 99;
  stroke: rgba(172, 255, 47, 0.774);
  fill: rgba(172, 255, 47, 0.774);
  // stroke-width: 1;
}
.device:hover {
  fill: rgb(42, 255, 255);
  stroke: rgba(47, 255, 255, 0.774);
}

.device:active {
  fill: rgba(213, 47, 255, 0.774);
  stroke: rgba(213, 47, 255, 0.774);
}

.contextPanel {
  padding: 4px;
  position: absolute;
  width: 480px;
  height: 400px;
  border-radius: 6px;
  background-color: rgba(43, 41, 41, 0.938);
  z-index: 1000;
}
.contextPanelLabel {
  text-align: center;
  margin-top: 6px;
}

.contextPanelLabelValues {
  margin-left: 6px;
}

.contextPanelCommands {
  height: 70%;
  margin: 4px;
  padding: 4px;
  background-color: rgba(221, 221, 221, 0.63);
}

.contextPanelCommandsLabel {
  text-align: center;
}

.wrapper {
  display: flex;
  width: 20px;
  align-items: center;
  flex-direction: column;
  & ul {
    padding: 0;
    & li {
      list-style: none;
    }
  }
}
