@import "../../Utils/colorScheme.scss";

.externalLinkTile {
  height: 200px;
  width: 210px;
  background-color: rgba(97, 97, 97, 0.712);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  margin-left: 20px;
}
.externalLinkTile:hover {
  background-color: rgba(170, 170, 170, 0.712);
}

.externalLinkTile > p {
  text-align: center;
  font-size: 28px ;
  color: $master-green;
}

.tilesLayout {
  display: flex;
  height: 200px;
  width: 100%;
  // border: 1px solid white;
  background-color: rgba(36, 36, 36, 0.144);
}

.tileImg{
  position: inherit;
  height: 100%;
  z-index: 1;
}

