.minimapWrapper {
	width: 100%;
	height: auto;
	overflow: hidden;
	box-shadow: 0px 0px 4px 3px rgb(0 0 0 / 20%);
	background: transparent;
	position: relative;
	user-select: none;
	/* text-align: center; */
}

.imageWrapper {
	position: relative;
	user-select: none;
}

.minimap {
	bottom: 10px;
	left: 10px;
	box-shadow: 0px 0px 4px 3px rgb(163 255 233 / 70%);
	background: transparent;
	position: absolute;
	user-select: none;
}

.markerFrame {
	position: absolute;
	cursor: move;
	box-shadow: inset 0px 0px 6px 6px rgb(163 192 255);
	user-select: none;
}

.minimapInsideContainer {
	position: relative;
}

.buttonGroup {
	width: 25px;
	position: absolute;
	right: 10px;
	bottom: 10px;
}

.button {
	width: 25px;
	height: 25px;
	margin-bottom: 2px;
}

.wraptest{
	box-sizing: border-box;
	display: block;
	border: 1px solid white;
	width: calc(100% - 800px);
}

.insideComponent {
	position: absolute;
	top: 10px;
	left: 10px;
}
.image{
	color: aqua;
	stroke: aqua;
}