@import "./colorScheme";

.commonScreen {
  position: relative;
  height: 100vh;
  width: 100%;

  &DarkCold {
    background-color: $master;
    color: $master-blue;
    a{
      color: rgb(105, 255, 180);
    }
    a:hover {
      color: hotpink;
    }
  }

  &DarkWarm {
    background-color: $master-dark-warm;
    color: $master-orange;
    a{
      color: rgb(105, 255, 180);
    }
    a:hover {
      color: hotpink;
    }
  }

  &LightCold {
    background-color: $master-light-cold;
    color: rgb(36, 149, 255);
    // text-shadow: 0px 0px 1px rgb(85, 85, 85);
    // font: 1em sans-serif;
    a{
      color: rgb(111, 0, 255);
    }
    a:hover {
      color: hotpink;
    }
  }

  &LightWarm {
    background-color: $master-light-warm;
    color: rgb(255, 102, 0);
    // text-shadow: 0px 0px 1px rgb(90, 90, 90);
    a{
      color: rgb(111, 0, 255);
    }
    a:hover {
      color: hotpink;
    }
  }
}

.commonScreenContent {
  padding: 6px;
  position: relative;
  z-index: 100 !important;
}

.commonFloatingContent {
  opacity: 0.95;
  // width: 98%;
  margin-left: 1%;

  // &LightCold{
  // }
}

.commonCardContainer {
  padding: 10px;
  min-width: 200px;

  &DarkCold {
    background-color: rgba(110, 110, 110, 0.363);
  }

  &DarkWarm {
    background-color: rgba(94, 93, 93, 0.363);
  }

  &LightCold {
    background-color: rgba(223, 223, 223, 0.363);
  }

  &LightWarm {
    background-color: rgba(223, 223, 223, 0.363);
  }
}

.commonHeaderImg {
  position: relative;
  height: 1em;
  // width: 80%;
  z-index: 30;
  pointer-events: none;
  margin-right: 6px;

  &DarkCold {
    fill: $master-blue;
  }

  &DarkWarm {
    fill: $master-orange;
  }

  &LightCold {
    fill: rgb(0, 140, 255);
  }

  &LightWarm {
    fill:  rgb(255, 102, 0);
  }
}
