@import "./Utils/colorScheme";

#root {
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.appContent {
  width: 100%;
  background-color: $master;
  min-width: calc(100% - 300px);
  max-width: calc(100% - 80px);
}

.appSideBar {
  background-color: $sideBarBackground;
  z-index: 1000;
}

.application {
  display: flex;
  flex-direction: row;
  height: 100vh;
}

.css-1k88rtw {
  border-right: none !important;
}

.app-title-bar {
  user-select: none;
  -webkit-user-select: none;
  -webkit-app-region: drag;
  height: 40px;
  width: 100%;
  background-color: #2f3241;
}

.bar-collapse-btn {
  height: 60px;
  // margin-left: 15px;
  padding-left: 15px;
  margin-top: 6px;
  // width: 100%;
}

.bar-collapse-btn:hover {
  background-color: $sb_btn_hover;
}
.app-logo {
  animation-duration: 0.5s;
  animation-name: slidein;
  height: 50px;
  margin-top: 8px;
  fill: $master-blue;
}

.app-logo-expanded {
  animation-duration: 0.5s;
  animation-name: slideout;
  height: 50px;
  margin-top: 8px;
  margin-left: 80px;
  fill: $master-blue;
  filter: drop-shadow(0px 2px 6px rgb(255, 196, 0));
}

@keyframes slideout {
  from {
    margin-left: 0px;
    filter: drop-shadow(0px 0px 0px rgb(0, 0, 0));
  }

  to {
    margin-left: 80px;
    filter: drop-shadow(0px 2px 6px rgb(255, 196, 0));
  }
}

